.banner-part {
    /* height: 100vh; */
    // position: relative;
    // display: flex;
    // flex-wrap: wrap;
    // align-items: center; /*vertically center items */
    // justify-content: center; /*horizontally center items*/
    // text-align: center;

    .banner-slider{
        // padding: 65px 0;
        position: relative;
        display: flex;
            height: 100vh;
       
            

        .slick-track{
            display: flex !important;
            height: 100%;
        }

        .slick-slide{
            position: relative;
            height:auto;
            @include flex(wrap,center,center);
        }

      
        

        // &::before{
        //     /* For  button to stay inside div element*/
        // content: '';
        // height: 100vh;
        // display: block;
        // }
    .banner-img{
        &::after{
            content: "";
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(191, 61, 66, 1);
        mix-blend-mode: darken;
        pointer-events: none;

        }  
    }
        
        
    }
    h1{
        color: $white;
    }

 
  }

  .banner-content {
    position: relative;
    z-index: 9;
    color: #fffdfd;
    max-width: 500px;
    text-align: center;

    p{
        margin: 0 0 25px;
    }
  }

  


//////////////////
  .about-part {
    padding: 120px 0;

    .image-wrap {
    position: relative;
    padding-top: 60%;
    overflow: hidden;
    border-radius: 24px;
  }

  p{
    margin: 0;
  }

  .btn-wrap {
    margin-top: 60px;
  }


}



  
  .products-part{
    text-align: center; 

    h2{
        line-height: 1;
    margin-bottom: 17px; 
    }
.style{
    &.style::after{
        top: -6px;
    }
}

    p{
        margin: 0;
 
    }

    .product-details{
        background-color:#121212;
        border-radius: 23px;
        overflow: hidden;
        color:#fff;

        
        p{
            margin: 0 0 32px;
        }

        
    }

    .col{
        .product-image{
            height: 400px;
            // display: flex;
            // flex-wrap: wrap;
            // align-items: center;
            // justify-content: center;
            @include flex(wrap,center,center);
         

            position: relative;
        }
        &:nth-child(2)
        { 
    .product-image
    {
height:305px;
    }
}

&:nth-child(3)
{ 
.product-image
{
height:250px;
background-size: auto;

}
}

&:last-child
{ 
.product-image
{
    height:182px;
}
}


}



.product-content{
    padding:0 13px 20px;
    font-size: 14px;
    line-height: 1.57;

    h3{
        margin-bottom: 20px;
        padding: 0;
        color: #fff;
    }
  }

  .product-code{
    // width: 62px;
    // height: 62px;

    @include size(62px);
    border-radius: 50%;
    color: #fff;
  background-color:#191919;
  font-family: 'Poppins',sans-serif;
  font-size: 25px;
  border: 2px solid #b03d42;
  line-height: 62px;
  text-align: center;
  margin: -30px auto 13px;
  position: relative;
  z-index:9;
  
  }
  
  .button-wrap{
    padding: 15px 0;

    .btn{
        text-transform: capitalize;
    &.primary{
        border: 2px solid #b03d42;
        }
    }
        
   
  }

  }


  .process-part{
    background-color: $dark-black;
    text-align: center;
    color:$white;
    padding: 100px 0;

    .container
{
    max-width:1007px;
}

    h2{
       
        color:$white;
        
    }

    .heading-wrap{
        max-width: 940px;
        margin: 0 auto 85px;

      
    }
        .icon-wrap{
            border: 1px solid $white;       
            border-radius: 9px;
            height: 148px;
        //     display: flex;
        //     flex-wrap: wrap;
        // align-items: center;
        // justify-content: center;
        @include flex(wrap,center,center);

        margin-bottom: 22px;
        }

        .process-text{
            //display: inline-block;
            //vertical-align: top;
            @include align-vertical(top);
            font-size: 20px;
        }
        .four-columns{
margin: 0 -31px 80px;
            .col{
            padding: 0 31px;
            }
        }

        .btn{
            text-transform: capitalize;
        }
    
  }


  .feedback-part{
text-align: center;
padding: 100px 0;
      .feedback-img{
          
          @include size(158px);
          margin: 0 auto;
          border-radius: 50%;
          border: 1px solid $red;
          overflow: hidden;
          

          img {
          object-fit: cover;
          width: auto; 
          height: 100%;
          }
        }

          cite{
           font-size: 19px;
           line-height: 1.57;
           text-transform: capitalize;
           display: block;
           font-style: normal;
          }

          q{
            quotes: none;
            display: inline-block;
            vertical-align: top;
          }

    
  


  .slick-arrow{
    font-size: 52px;
    color: $red;
    line-height: 1;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 9;
 

    
    &.slick-prev
    {
        left: 0;
    }

    &.slick-next{
        right: 0;
    }
i{
    @include animate(color,0.20s,linear);
}
    &:hover{       
        i{
            color: darken($red,10%);
            
        }
    }

  
  }
  .feedback-slider{
    padding: 0 60px;
}

}
 
  
  
.slick-dots{
        @extend %listreset;
        position: absolute;
        bottom:72px;
        left:0;
        right:0;
        display:flex;
        @include flex(wrap,center,center);


        li{
            padding: 0 2px 0 1px;

            &.slick-active{
                button{
                    @include size(26px);
                    background-color: transparent;
        
                }
            }
        }
        button{
            padding:0;
            display: block;
            @include hide-text;
            background-color: #f9f9fb;
            @include size(18px);
            border-radius: 50%;
            border:3px solid #f9f9fb;
        @include animate(all,0.35s,ease);

            &:hover{
                @include size(26px);
                background-color: transparent;
            }
        }

}

  

  
  
